#break,#session{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.edit-break,.edit-session{
    display: flex;
}
#counter{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#session-length, #break-length{
    margin: 0 1em;
    font-size: 2em;
}
#break button, #session button{
    border: none;
    font-size: 2em;
    color: beige;
    background-color: transparent;
}
#break-label, #session-label{
    font-size: 2em;
}