#time-left{
    font-size:4em;
}
.timer{
    text-align: center;
    border: 0.5em solid white;
    padding: 1em 4em;
    border-radius: 2em;
}
.controls{
    display: flex;
    justify-content: space-around;
}
.controls button{
    font-size: 1.5em;
    background-color: transparent;
    border: none;
}
#timer-label{
    font-size: 2em;
}